const apiKey = "d4afeb7f2df1e4c2958dda920a7232b6";
const geoCodingUrl = "https://api.openweathermap.org/geo/1.0/direct";
const currentWeatherUrl = "https://api.openweathermap.org/data/2.5/weather";

const ipinfoToken = "246183c090dd15";
const ipinfoUrl = `https://ipinfo.io/json?token=${ipinfoToken}`;

async function getLocationByIP() {
  try {
    const response = await fetch(ipinfoUrl);
    if (!response.ok) {
      throw new Error(`–û—à–∏–±–∫–∞ —Å–µ—Ç–∏: ${response.statusText}`);
    }
    const data = await response.json();
    return {
      city: data.city,
      lat: data.loc.split(",")[0],
      lon: data.loc.split(",")[1],
    };
  } catch (error) {
    console.error(
      `–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –¥–∞–Ω–Ω—ã—Ö –æ –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–∏: ${error.message}`
    );
    throw error;
  }
}

async function getCoordinatesByCity(city) {
  if (!city) {
    throw new Error("–ù–∞–∑–≤–∞–Ω–∏–µ –≥–æ—Ä–æ–¥–∞ –Ω–µ –∑–∞–¥–∞–Ω–æ.");
  }

  try {
    const response = await fetch(
      `${geoCodingUrl}?q=${encodeURIComponent(city)}&limit=1&appid=${apiKey}`
    );
    const data = await response.json();

    if (!Array.isArray(data) || data.length === 0) {
      throw new Error(`–ì–æ—Ä–æ–¥ "${city}" –Ω–µ –Ω–∞–π–¥–µ–Ω.`);
    }

    const { lat, lon } = data[0];
    return { lat, lon };
  } catch (error) {
    console.error(`–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç: ${error.message}`);
    throw error;
  }
}

async function getWeatherByCoordinates(lat, lon) {
  try {
    const url = `${currentWeatherUrl}?lat=${lat}&lon=${lon}&units=metric&lang=ru&appid=${apiKey}`;

    const response = await fetch(url);

    if (!response.ok) {
      throw new Error(`–û—à–∏–±–∫–∞ —Å–µ—Ç–∏: ${response.statusText}`);
    }

    const weatherData = await response.json();
    return weatherData;
  } catch (error) {
    console.error(`–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –¥–∞–Ω–Ω—ã—Ö –æ –ø–æ–≥–æ–¥–µ: ${error.message}`);
    throw error;
  }
}

async function getWeatherByCity(city) {
  try {
    if (!city) {
      throw new Error("–ù–∞–∑–≤–∞–Ω–∏–µ –≥–æ—Ä–æ–¥–∞ –Ω–µ –∑–∞–¥–∞–Ω–æ.");
    }
    const { lat, lon } = await getCoordinatesByCity(city);
    return await getWeatherByCoordinates(lat, lon);
  } catch (error) {
    console.error(`–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –¥–∞–Ω–Ω—ã—Ö –æ –ø–æ–≥–æ–¥–µ: ${error.message}`);
    throw error;
  }
}

async function getWeatherByIP() {
  try {
    const location = await getLocationByIP();
    return await getWeatherByCoordinates(location.lat, location.lon);
  } catch (error) {
    console.error(
      `–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –¥–∞–Ω–Ω—ã—Ö –æ –ø–æ–≥–æ–¥–µ –ø–æ IP: ${error.message}`
    );
    throw error;
  }
}

export { getWeatherByCity, getWeatherByCoordinates, getWeatherByIP };
